.product{
    display: flex;
    flex-direction: column;
    border: 1px solid #000000;
    border-radius: 10px;
    padding: 15px;
    width: 170px;
    height: 200;
}

.img {
    width: 100%;
    background: lightgray;
    height: 100%;
    margin: 0 auto;
}

.description{
    font-size: 0.8em;
}

.add-btn {
    width: 100%;
    border-radius: 10px;
    border-width: 1px;
    background-color: #fca708;
    border-color: black;
}